<template>
    <div class="miniforum">
      <div class="header">
        <i ></i><h1 style="font-family: 'UD Digi Kyokasho N-R';display: inline">今日新闻</h1>
        <div  class="more">
          <a href="#" style="font-family: 'UD Digi Kyokasho NP-R';font-weight: bold">更多></a>
        </div>
      </div>
      <div v-for="i in content" class="content">
        <a href=""><img :src="i.src" alt=""></a>
        <div class="description">
          <p style="font-weight: bold;font-family: 'UD Digi Kyokasho NK-B'">{{i.title}}</p>
          <p style="text-align: left;font-weight: bold;font-family: 'UD Digi Kyokasho NP-R'">
            <span>&trpezium;城市&nbsp;{{i.location}}&nbsp;&nbsp;&nbsp;</span>
          </p>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "miniforum",
      data() {
        return{
          content:[
            {'title':'头牌','src':'static/1.jpg','location':'杭州'},
            {'title':'昨天的头牌','src':'static/2.jpg','location':'福州'},
            {'title':'前天的头牌','src':'static/3.jpg','location':'杭州'},
            {'title':'被抛弃的头牌','src':'static/4.jpg','location':' 北京'},
            {'title':'差点被抛弃的头牌','src':'static/5.jpg','location':'宁夏'},
            {'title':'失宠的猫','src':'static/6.jpg','location':'青岛'},
            {'title':'被抛弃的猫','src':'static/7.jpg','location':'上海'},
            {'title':'成精的猫','src':'static/8.jpg','location':'北京'},
          ]
        }
      }
    }
</script>

<style scoped>
  * {
    padding: 0;
    margin: 0;
  }
  img {
    width: 90%;
    height: 65%;
    margin: 10px 0 auto;
    border-radius: 10px;
  }
  .description {
    width: 100%;
    height: 25%;
    /*background-color: #a2b4f5;*/
  }
.miniforum {
  height: 40%;
  width: 100%;
  margin: 100px 0 0;
  /*background-color: #fffd1e;*/
}
  .content {
    height: 35%;
    width: 20%;
    /*background-color: beige;*/
    margin:0 5% 2.5% 0;
    float: left;
  }
  .header {
    width: 100%;
    height: 10%;
    /*background-color: bisque;*/
    margin: 0 0 10px;
    text-align: left;
    border-bottom: 1px solid #c4c4c4;
  }
  i {
    display: inline-block;
    width: 50px;
    height: 50px;
    background-image: url("/static/1.jpg");
    background-size: 100% 100%;
    border-radius: 20px;
    position: relative;
    top: 10%;
    margin: 0 40px 0 0;
  }
  a {
    text-decoration: none;
  }
  .more {
    width: 100px;
    height: 50px;
    position: relative;
    right: -1100px;
    display: inline;
  }
  .more a {
    border: 1.5px solid #acacac;
    border-radius: 5px;
  }
  img:hover {
    width: 300px;
    height: 200px;
    transition: .5s;
    position:relative;
    border: 1px #000000 solid;
     }
  img {
    transition: 1s;
    border: 1px #000000 solid;
  }
</style>



