<template>
  <div class="maojiaoyou_index">
    <div class="header"><a class="title" style="cursor: pointer">猫猫交友 </a></div>
    <ul>
      <li v-for="(it,index) in content">
        <img :src="'static/'+(index+6)+'.jpg'" class="chat_header"> <dl>
        <dt>
          <a class="title" href="#" >
            {{it.title}}
          </a>
        </dt>
        <dd>
          <em>今日新帖：{{it.account}}</em>
        </dd>
        <dd>
          <a href="#">{{it.recent.author}}</a><em>&numsp;{{it.recent.date}}</em><em>&numsp;{{it.recent.title}}</em>
        </dd>
      </dl>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "index",
      data() {
          return{
            content:[
              {'title':'自家主子','account':'25535','recent':{'author':'pipi美','date':'2018-08-03','title':'我家主子今天终于肯给我顺毛啦'}},
              {'title':'今日新猫','account':'11452','recent':{'author':'popo子','date':'2018-08-03','title':'大家好我是小黑'}},
            ]
          }
      }
    }
</script>

<style scoped>
  * {
    margin: 0px;
    padding: 0px;
    text-decoration-line: none;
  }
  a {
    color: black;
  }
  .header {
    width: 100%;
    height: 30%;
    border-bottom:1px solid #9898b8;
    border-top:1px solid #9898b8;
    background: linear-gradient(#def1ff,#bfbfbf);
  }
  .header a {
    position: relative;
    top: 16%;
    margin: 0 20px;
  }
  .maojiaoyou_index {
    text-align: left;
    width: 100%;
    height: 10%;
    /*background-color: #1ad1ff;*/
    display: block;
    margin: 52px auto;
  }
  ul {
    height: 304%;
    width: 100%;
    list-style-type: none;
  }
  li {
    border-bottom: 1px #c9c9c9 dashed ;
    float: left;
    width: 50%;
    height: 33.3%;
  }

  dl {
    margin: 0 0 50px 0;
    margin-left: 15%;
  }
  dt {
    margin: 20px 0 20px 0;
  }
  dd {
    margin:10px 0 0 0;
  }
  .chat_header {
    width: 75px;
    height: 75px;
    position: absolute;
    border-radius: 5px;
    border: #151e2a 1px solid;
    margin: 2.5% 0
  }
  .title {
    font-family: "Microsoft JhengHei";
    font-weight: bold;
    font-size: 20px;
    text-decoration-line: none;
    color: black;

  }
  .title:hover {
    text-decoration-line: underline;
  }
</style>



