<template>
    <div class="mininews">
      <div class="header">
        <i ></i><h1 style="font-family: 'UD Digi Kyokasho N-R';display: inline">论坛</h1>
        <div  class="more" >
          <a href="#" style="font-family: 'UD Digi Kyokasho NP-R';font-weight: bold">更多></a>
        </div>
    </div>
