        <div v-for="i in content" class="content">
          <a href=""><img :src="i.src" alt=""></a>
          <div class="description">
            <p style="font-weight: bold;font-family: 'UD Digi Kyokasho NK-B'">{{i.title}}</p>
            <p style="text-align: left;font-weight: bold;font-family: 'UD Digi Kyokasho NP-R'"> <span>&trpezium;作者&nbsp;{{i.author}}&nbsp;&nbsp;&nbsp;&nbsp;</span></p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import chatbubble from '@/components/chatbubble';
    export default {
        name: "mininews",
      data() {
        return{
          content:[
            {'title':'猫猫一号','src':'static/9.jpg','author':'雷军'},
            {'title':'猫猫二号','src':'static/10.jpg','author':'马腾'},
            {'title':'猫猫三号','src':'static/11.jpg','author':'马超'},
            {'title':'猫猫四号','src':'static/12.jpg','author':'黄忠'},
            {'title':'猫猫五号','src':'static/13.jpg','author':'姜文'},
            {'title':'猫猫六号','src':'static/14.jpg','author':'卖鸡小子'},
          ]
        }
      },
      components:{
          chatbubble,
      }
    }
</script>

<style scoped>
.mininews {
  height: 40%;
  width: 100%;
  margin: 10px 0 0;
  /*background-color: #fffd1e;*/
}
* {
  padding: 0;
  margin: 0;
}
img {
  width: 252px;
  height: 145px;
  margin: 10px 0 auto;
  border-radius: 10px;
  border: 1px #000000 solid;
}
.description {
  width: 100%;
  height: 25%;
  /*background-color: #a2b4f5;*/
}
.content {
  height: 35%;
  width: 20%;
  /*background-color: beige;*/
  margin:0 10% 2.5% 0;
  float: left;
}
.header {
  width: 100%;
  height: 10%;
  /*background-color: bisque;*/
  margin: 0 0 10px;
  text-align: left;
  border-bottom: 1px solid #c4c4c4;
}
i {
  display: inline-block;
  width: 50px;
  height: 50px;
  background-image: url("/static/15.jpg");
  background-size: 100% 100%;
  border-radius: 20px;
  position: relative;
  top: 10%;
  margin: 0 40px 0 0;
}
  a {
    text-decoration: none;
  }
.more {
  width: 100px;
  height: 50px;
  position: relative;
  right: -1200px;
  display: inline;
}
  .more a {
    border: 1.5px solid #acacac;
    border-radius: 5px;
  }
</style>



