<template>
  <div id="app">
    <ul class="topnav">
      <li>
        <router-link to="/" class="link">首页</router-link>
      </li>
      <li >
        <router-link to="/Forum" class="link">论坛</router-link>
      </li>
      <li >
        <router-link to="/news" class="link">新闻</router-link>
      </li>
      <li>
        <router-link to="/resume" class="link">联系我们</router-link>
      </li>
    </ul>
    <router-view/>
    <side-nav></side-nav>
  </div>
</template>

<script>

import topNav from '@/components/topNav'

import sideNav from '@/components/sideNav'
export default {
  name: 'App',
  components:{
    topNav,
    sideNav
  }
}
</script>

<style>
#app {
  position: absolute;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  z-index: -1;
}
* {
  margin: 0;
  padding: 0;
}
.topnav {
  position: fixed;
  top: 0;
  width: 100%;
}

.topnav  li {
  position: static;
  margin: auto auto;
  background: #ffffff;
  list-style-type: none;
  float: left;
  font-family: "UD Digi Kyokasho NP-B";
  font-size: 2em;
  width: 24.9%;
  border-bottom:1px solid #acacac;
  border-radius: 0 0 5px 5px;
  opacity: 0.5;
}
.topnav  li:not(:last-child) {
  border-right:1px solid #acacac;

}
.topnav ul li a {
  display: inline;
  position: absolute;
  width: 100%;
  height: 100%;
}
.topnav  li .link {
  position: static;
  text-decoration-line: none;
  color: #000000;
  width: 100px;
  height: 50px;
}
.topnav li .link:hover {
  color: #ffffff;
  background: #006dff;
  transition: .8s;
}
.topnav  li .link {
  display: block;
  margin: 0 auto;
  width: 100%;
}
</style>



