</script>

<style scoped>
  .main_content {
    margin: 0;
    padding: 0;
    position: absolute;
    height: 1600px;
    width: 75vw;
    top: 14vh;
    left: 12.5vw;
    /*background-color: aquamarine;*/
    z-index: -1;
  }
</style>



