<template>
  <div class="hobby">
    <h1>兴趣爱好</h1>
    <img v-for="(i,index) in hobby" :src="i.src" :class="'a'+index" alt="兴趣爱好" class="hobby_image">
  </div>
</template>

<script>
    export default {
        name: "hobby",
      data(){
        return{
          hobby:[
            {'src':'static/anime.jpg'},
            {'src':'static/dota2.jpg'},
            {'src':'static/zuqiu.jpg'},
            {'src':'static/15.jpg'},
          ]
        }
      }
    }
</script>

<style scoped>
  * {
    margin: 0px;
    padding: 0px;
  }
.hobby_image {
  width: 25vw;
  height: 20vh;
  margin: 7vh;
  margin-left: 2.5vw;
  border-radius: 5px;
  border: black solid 2px;
  /*animation: float 1s ease-in-out infinite alternate;*/
}
.a0 {
  animation: float 1s ease-in-out infinite alternate;
}
.a1 {
  animation: float2 .5s ease-in infinite alternate;
}
.a2 {
  animation: float 2s ease-out infinite alternate;
  }
.a3 {
  animation: float2 .8s ease infinite alternate;
}
  @keyframes float {
  from{ transform: translate(0,0);}
  to{transform: translate(0,20px);}
  }
@keyframes float1 {
  from{ transform: translate(0,0);}
  to{transform: translate(0,10px)}
}
@keyframes float2 {
  from{ transform: translate(0,0);}
  to{transform: translate(0,30px)}
}
</style>



