import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home/Home'
import Forum from '@/pages/Forum/index'
import resume from '@/pages/resume/index'
import news from '@/pages/news/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Forum',
      name: 'Forum',
      component: Forum
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/resume',
      name: 'resume',
      component: resume
    }
  ]
})



