<template>
      <div class="tag" style="display: inline-block">
        <img class="qrcode" :src="'static/'+(index+10)+'.jpg'">
        <div class="arrow">
          <em></em><span></span>
        </div>
      </div>
</template>

<script>
    export default {
        name: "chatbubble",
      props:['index'],
    }
</script>

<style scoped>
.chatbubble {
  position: relative;
  left: 100px;
}
.tag{
  margin: 0 auto;
  width:120px;
  height:120px;
  position:absolute;
  background-color: #ffffff;
  border: 1px solid #000000;
  border-radius: 5px;
  top:-55px;
}
.arrow{
  position:absolute;
  width:10px;
  height:10px;
  bottom:-10px;
  left:50px;
}
.arrow *{
  display:block;
  border-width:10px;
  position:absolute;
  border-style:solid dashed dashed dashed;
  font-size:0;line-height:0;
}
.arrow em{border-color: #000000 transparent transparent;}
.arrow span{border-color:#FFF transparent transparent;top:-1px;}
.qrcode {
  width: 110px;
  height: 110px;
  margin: 5px;
  border-radius:5px ;
}
</style>



