<template>
  <div class="introduce">
    <img src="static/15.jpg" class="portrait">
    <div class="skill_introduce">
      <h1>自我介绍</h1>
      <p>有一定的，html，css，JavaScript知识基础。能够结合bootstrap独立进行简单的静态页面开发。并且根据需求进行修改。</p>
      <p>对Jquery有一定的认识。能够独立使用Jquery进行简单的页面行为设计。</p>
      <p>对ajax有简单的了解。对jason数据有简单的了解。</p>
      <p>可以使用c语言结合单片机的特性进行简单的功能开发。</p>
      <p> 做事有条理。乐于与人沟通。</p>
      <h1>主修课程</h1>
      <p>通信原理,
      现代交换技术与原理,
      信号与系统,
      单片机原理与应用,
      数字信号处理,
      模拟电子技术,
      嵌入式技术,
      MATLAB应用</p>
    </div>
  </div>
</template>

<script>
    export default {
        name: "index"
    }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
  .introduce {
    position: absolute;
    width: 20vw;
    height: 80vh;
    border:2px solid #000;
    border-radius: 5px;
    margin: 7vh 5vw;
  }
  .portrait {
    width: 12vw;
    height: 12vw;
    margin: 2.5vw 0 0 0;
    border-radius: 50%;
    border:1px solid #000000;
    animation:rotate 20s infinite linear;
  }
  @keyframes rotate{
    0%{
      transform:rotate(0deg);
    }
    100%{
      transform:rotate(360deg);
    }
  }
  .skill_introduce{
    width: 18vw;
    height: 44vh;
    margin: 2vh auto;
    /*border-radius: 5px;*/
    /*border:2px solid #000000;*/
    text-align: left;
    padding: 5px;
    text-indent: 2em;
  }
  p {
    margin: 10px 0;
    text-shadow: 0px 0px 1px #000;
  }
</style>



