<template>
  <div class="yangmao_index">
    <div class="header"><a class="title" style="cursor: pointer">养猫交流 </a></div>
    <ul>
      <li v-for="(it,index) in content">
       <img :src="'static/'+(index+1)+'.jpg'" class="chat_header"> <dl>
          <dt>
            <a class="title" href="#" >
              {{it.title}}
            </a>
          </dt>
          <dd>
            <em>今日新帖：{{it.account}}</em>
          </dd>
          <dd>
            <a href="#">{{it.recent.author}}</a><em>&numsp;{{it.recent.date}}</em><em>&numsp;{{it.recent.title}}</em>
          </dd>
        </dl>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "index",
      data() {
          return{
            content:[
              {'title':'猫猫娱乐','account':'23898','recent':{'author':'神谷哲史','date':'2018-07-05','title':'夏季猫猫最喜欢的竟然是！'}},
              {'title':'每日养猫经','account':'21452','recent':{'author':'手冢治虫','date':'2018-06-01','title':'震惊猫猫居然要这样养'}},
              {'title':'精致猫猫','account':'3256','recent':{'author':'宫崎骏','date':'2018-07-23','title':'猫猫起来要干这三件事！'}},
              {'title':'有猫病','account':'456','recent':{'author':'艾伦.耶格尔','date':'2018-08-01','title':'关于猫猫患了蛔虫病比较好的处理方法'}},
              {'title':'猫猫美容','account':'123545','recent':{'author':'尼古拉斯凯奇','date':'2018-05-11','title':'夏天了,要怎么样让猫猫既美观又舒适呢？'}},
            ]
          }
      }
    }
</script>

<style scoped>
* {
  margin: 0px;
  padding: 0px;
  text-decoration-line: none;
}
a {
  color: black;
}
.header {
  width: 100%;
  height: 10%;
  border-bottom:1px solid #9898b8;
  border-top:1px solid #9898b8;
  background: linear-gradient(#def1ff,#bfbfbf);
}
.header a {
  position: relative;
  top: 16%;
  margin: 0 20px;
}
  .yangmao_index {
    text-align: left;
    width: 100%;
    height: 30%;
    /*background-color: #1ad1ff;*/
  }
  ul {
    height: 100%;
    width: 100%;
    list-style-type: none;

  }
li {
  border-bottom: 1px #c9c9c9 dashed ;
  float: left;
  width: 50%;
  height: 33.3%;
}

  dl {
    margin: 20px;
    margin-left: 15%;
  }
  dt {
    margin: 20px 0;
  }
  dd {
    margin: 10px 0;
  }
  .chat_header {
      width: 75px;
      height: 75px;
      margin: 2.5% 0;
      position: absolute;
    border-radius: 5px;
    border: #151e2a 1px solid;
  }
  .title {
    font-family: "Microsoft JhengHei";
    font-weight: bold;
    font-size: 20px;
    text-decoration-line: none;
    color: black;
  }
  .title:hover {
    text-decoration-line: underline;
  }
</style>



