<template>
      <div>
        <select id="select" >
        <option @click="show(index)" v-for="(i,index) in exp" :value="i.date">
        {{i.date}}
      </option>
      </select>
        <h1 class="tittle">{{exp[flag].title}}</h1>
        <div class="include"><p class="exp-body" >{{exp[flag].expr}}</p></div>
      </div>
</template>

<script>
    export default {
        name: "experience",
        data(){
          return{
            exp:[
              {"date":"2014.9-2017.6",
                "title":"校级足球俱乐部部长",
                 "expr": "组织校级的足球比赛，负责比赛的策划，组织，人员联系，赛程进行以及仲裁。在任期间举行了多次比赛"},

              {"date":"2016.05-2016.6",
                "title":"本地数据采集上位机的设计",
                "expr": "基于C#的上位机设计，使用串口进行通信，采用VS进行开发，在项目中我负责进行上位机的整体设计，包括图形界面，数据的显示"},

              {"date":"2017.10-2018.2",
                "title":"基于wifi的无线智能家居系统",
                "expr": "设计一个基于WiFi的简单的无线智能家居控制系统，主要负责进行单片的功能开发以及数据的封装。单片机选用stm32开发，通信模块选用esp8266进行开发。上位机的开发使用的是Qt"},

            ],
            flag:0
          }
        },
      methods:{
        show(a) {
          this.flag=a;
          console.log(a)
          console.log(this.exp[a].title)
        }
      }
    }
</script>

<style scoped>
select {
  font-size: 4vw;
  border-radius: 5px;
  margin: 10vh;
}
  .tittle {
    font-size: 4vw;
  }
  .exp-body {
    margin: 2vh auto;
    font-size:2.5vw;
    /*color: #ffbae3;*/
    text-align: left;
  }
  .include {
    width: 50vw;
    margin: 0 auto;
    text-indent: 4vw;
  }
</style>



