<template>
    <div class="topnav">
      <ul>
        <li v-for="value in topNavcontent" :class="{navhover:ishover}">
            <router-link><a target="_blank" @mouseenter={hovertest,hovertest} :href="'http://'+value.url" >{{value.name}}</a></router-link>
        </li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "topNav",
      data:function () {
        return {
          topNavcontent:[
            {
              'name':'首页',
            'url':'www.bilibili.tv'
            },
            {
              'name':'新闻',
              'url':'www.bilibili.tv'
            },
            {
              'name':'论坛',
              'url':'www.bilibili.tv'
            },
            {
              'name':'联系我们',
              'url':'www.bilibili.tv'
            }
          ],
          ishover:true
        }
      },
      methods:{
          hovertest(){
            this.ishover=!this.ishover,
             console.log('mousemovein')
             console.log(this.ishover)
          }
      }
    }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
.topnav {
  position: fixed;
  top: 0;
  width: 100%;
}

  .topnav ul li {
    position: static;
    margin: auto auto;
    background: #ffffff;
    list-style-type: none;
    float: left;
    font-family: "UD Digi Kyokasho NP-B";
    font-size: 2em;
    width: 24.9%;
    border-bottom:1px solid #acacac;
    border-radius: 0 0 5px 5px;
    opacity: 0.5;
  }
  .topnav ul li:not(:last-child) {
    border-right:1px solid #acacac;

  }
  .topnav ul li a {
    display: inline;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  /*.topnav ul li:hover {*/
    /*background: #1aafef;*/
    /*color: aliceblue;*/
    /*transition: 1s;*/
  /*}*/
  .topnav ul li a {
    position: static;
    text-decoration-line: none;
    color: black;
    width: 100px;
    height: 50px;
  }
  .topnav ul li a:hover {
    color: #ffffff;
    background: #006dff;
    transition: .8s;
    border-radius: 5px;
  }
  .topnav ul li a {
    display: block;
    margin: 0 auto;
    width: 100%;
  }
</style>



