<template>
    <div class="footerdiy">
      <ul>
        <li><h1>联系方式</h1><br><a v-for="i in communication">{{i.name}}</a></li>
        <li><h1>传送门</h1><br><a v-for="i in complain">{{i.name}}</a></li>
        <li>
          <h1>其他方式</h1><br>
          <div v-for="(it,index) in others" style="display: inline">
            <transition name="footer"><chatbubble v-show="flag===index" :index="index" ></chatbubble></transition>
            <img ref="test" @mouseenter="bubble_event($event)" @mouseleave="bubble_event_leave"  :src="it.src" class="others">
          </div>
        </li>
      </ul>

    </div>
</template>

<script>
  import chatbubble from '@/components/chatbubble';
    export default {
        name: "footer",
      components:{
        chatbubble,
      },
      data() {
          return{
              communication:[
                {'name':'请联系我'},
                {'name':'请联系我'},
                {'name':'请联系我'},
                {'name':'请联系我'},
                {'name':'请联系我'},
                {'name':'请联系我'},
              ],
              complain:[
                {'name':'请联系我'},
                {'name':'请联系我'},
                {'name':'请联系我'},
                {'name':'请联系我'},
                {'name':'请联系我'},
                {'name':'请联系我'},
                {'name':'请联系我'},
              ],
              others:[
                {'src':'static/1.jpg'},
                {'src':'static/2.jpg'},
                {'src':'static/3.jpg'},
              ],
              flag:4,

          }
      },
      mounted() {

        console.log(this.$refs.test[0].offsetHeight);　
        console.log(this.$refs.test[0].offsetWidth);　

      },
      methods:{
        bubble_event(e) {
          console.log(e.target);
          if (e.target == this.$refs.test[0]) {
            this.flag = 0;
          }
          else if (e.target == this.$refs.test[1]) {
            this.flag = 1;
          }
          else if (e.target == this.$refs.test[2]) {
            this.flag = 2;
          }
          else {
            this.flag = 3;
          }
          console.log(this.flag);
        },
        bubble_event_leave() {
          this.flag = 4;
        }
      }
    }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
.footerdiy {
  position: relative;
  background-color: #f6f9fa;
  width: 100vw;
  height: 300px;
  left: -13vw;
}
ul {
  height: 100%;
  width: 100%;
}
a{
  display: block;
  float: left;
  height: 40%;
  width: 30%;
}
  li {
    margin:0 0 0 5%;
    float: left;
    height: 40%;
    width: 27%;
    list-style-type: none;
  }
  li:not(:last-child) {
    border-right:1px solid #e5e9ef;
  }
  .others {
    border-radius: 50%;
    width: 100px;
    height: 100px;
    margin: 10px;
  }
  h1 {
    text-align: left;
  }
  .footer-enter-active,.footer-leave-active {
    transition: opacity .5s;
  }
.footer-enter,.footer-leave-to {
  opacity: 0;
}

</style>



