<template>
  <div class="information_content">
      <swiper :options="swiperOption" style="height: 100%;width: 100%;">
        <swiper-slide>
          <div   class="informationbody">
                <hobby></hobby>
          </div>
        </swiper-slide>
        <swiper-slide>
          <div   class="informationbody">
            <experience></experience>
          </div>
        </swiper-slide>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
      </swiper>
  </div>
</template>

<script>
  import { swiper, swiperSlide } from 'vue-awesome-swiper';
  import hobby from './modle/hobby';
  import experience from './modle/experience'
    export default {
        name: "index",
      data(){
          return{
            images: [
              {'src': 'static/1.jpg'},
              {'src': 'static/2.jpg'},
              {'src': 'static/3.jpg'},
            ],
            swiperOption: {
              slidesPerView: 1,
              spaceBetween: 30,
              loop: true,
              pagination: {
                el: '.swiper-pagination',
                clickable: true
              },
              navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev'
              }
            }
          }
      },
      components:{
          hobby,
          experience
      }
    }
</script>

<style scoped>
  * {
    margin: 0px;
    padding: 0px;
  }
  .information_content {
    position: absolute;
    width: 100%;
    height: 100%;

  }
  .informationbody {
    width: 100%;
    height: 100%;
    /*border:1px solid #000;*/
    /*border-radius: 5px;*/
  }
</style>



