<style scoped>
  * {
    margin: 0px;
    padding: 0px;
  }
  .information {
    position: absolute;
    width: 65vw;
    height: 80vh;
    border:2px solid #000;
    border-radius: 5px;
    margin: 7vh 30vw;
  }
</style>



