<template>
    <div class="lunbo" >
      <swiper :options="swiperOption" style=";height: 100%;width: 100%;">
        <swiper-slide v-for="item in images"><img :src="item.src" alt="" style="border-radius: 10px"></swiper-slide>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>

</template>

<script>
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
    export default {
        name: "lunbo",
      data() {
        return {
          images: [
            {'src': 'static/1.jpg'},
            {'src': 'static/2.jpg'},
            {'src': 'static/3.jpg'},
            {'src': 'static/4.jpg'},
            {'src': 'static/5.jpg'},
            {'src': 'static/6.jpg'},
          ],
          swiperOption: {
            slidesPerView: 1,
            spaceBetween: 30,
            loop: true,
            autoplay:{
              delay:2500,
              disableOnInteraction:false
            },
            pagination: {
              el: '.swiper-pagination',
              clickable: true
            },
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev'
            }
          }
        }
      }
    }
</script>

<style scoped>
  .lunbo {
    width: 65%;
    height: 30%;
    border-radius: 10px;
    border: #222222 1px solid;
  }
  img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }
</style>



