<template>
    <div class="sidenav">
      <transition name="nav2">
        <p v-show="!isActive" style=" font-size:40px;width: 40px; padding: 5px" @mouseenter="test" class="navcontrol">
         导航<
        </p>
      </transition>
      <transition name="nav">
      <ul  v-show="isActive" @mouseleave="test">
        <li v-for="(key,index) in sidenavcontent">
            <p style="display: inline;border-radius: 5px;height: 70% ">{{index+1}}</p>{{key.name}}
        </li>
      </ul>
      </transition>
    </div>
</template>

<script>
    export default {
        name: "sidenav",
      data:function () {
        return{
          isActive:false,
          sidenavcontent:[
            {'name':'猫猫之家'},
            {'name':'养猫交流'},
            {'name':'您与猫猫'},
            {'name':'猫猫交友'},
            {'name':'带猫回家'},
            ]
        }
      },
     methods:{
          test(){
            this.isActive=!this.isActive;
          }
      }
    }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
.sidenav {
  position:fixed;
  right: 0px;
  top: 35vh;
  z-index: 1000;
}
.navcontrol {
  margin:35px ;
  cursor: pointer;
}
  .sidenav ul {
    list-style-type:none ;
    border-width:2px 0 2px 2px;
    border-style: solid none solid solid;
    border-color: #cdcdcd;

    border-radius:20px 0 0 20px;
    padding: 10px;
  }
  .sidenav ul li{
    font-family: "UD Digi Kyokasho NK-R";
    font-size: 200%;;
    cursor: pointer;
    border: 2px solid #ffffff;
    border-radius: 5px;

}
  .sidenav ul li:hover{
   background: #1ad1ff;
    transition: .5s;
  }
  .sidenav ul li:not(:last-child) {
    border-bottom-color: #b5b5b5;
  }
  .nav-enter-active,.nav-leave-active{
    transition: opacity .2s ;
  }
  .nav-enter,.nav-leave-to {
    opacity: 0;
  }
  .nav2-enter-active{
    transition: opacity 2s ;
  }
  .nav2-enter,.nav2-leave-to {
    opacity: 0;
  }
  span {
    user-select:none
  }
  li p:first-child {
    background-color:#ff5039;
  }
  .navcontrol {
   position: fixed;
    right: -20px;
    top: 35vh;
    border: 2px black solid;
    border-radius: 5px;
  }
</style>



